import http from '@/components/utils/http.js';
export default {
	data(){
		return{
			noname:"",
			bankname:"",
			bankno:"",
			itaccount:0,
			cashout:'',
			istruebank:false,
		}
	},
	methods:{
		navTo(url) {
			uni.navigateTo({
				url
			});
		},
		cashoutval(){
			
			this.cashout=this.itaccount;
		},
		
		_getcashout() {
			uni.showLoading({
				title:"加载中.."
			})
			let _this=this;
			let opts = {
				url: '/base/getcashout/',
				method: 'post'
			};
			let param = {};
			http.httpTokenRequest(opts, param).then(
				res => {
					//打印请求返回的数据
					if (res.data['code'] == 0) {
						uni.hideLoading();
						_this.itaccount=res.data['itaccount'];
						if(res.data['bankinfo']=='' || res.data['bankinfo']==undefined ||  res.data['bankinfo']==null){
							_this.istruebank=false;
							
						}else{
							_this.istruebank=true;
							_this.bankname=res.data['bankinfo'].bankname;
							_this.bankno=res.data['bankinfo'].bankno;
							_this.noname=res.data['bankinfo'].noname;	
						}
						
					} else {
						uni.hideLoading();
						
					}
				},
				error => {
					console.log(error);
				}
			);
		},
		
		submit(){
		if(this.cashout<10){
			this.$api.msg("当前提现金额少于10元");
			return;
		}	
		
		if(this.bankno==""){
			this.$api.msg("提现银行账号不能为空");
			return;
		}
			
		uni.showLoading({
			title:"加载中..",
			mask:true,
		})
		let _this=this;
		let opts = {
			url: '/base/submitcash/',
			method: 'post'
		};
		let param = {noname:this.noname,
			bankname:this.bankname,
			bankno:this.bankno,
			cashout:this.cashout
			};
		http.httpTokenRequest(opts, param).then(
			res => {
				//打印请求返回的数据
				if (res.data['code'] == 0) {
					uni.hideLoading();
					this.itaccount=res.data['itaccount'];
					this.$api.msg("提现成功");
					
				} else {
					uni.hideLoading();
					this.$api.msg(res.data["msg"]);
				}
			},
			error => {
				console.log(error);
			}
		);	
			
			
			
		}
		
	},
	
	onLoad() {
		this._getcashout();
	}

}
